.button {
  display: inline-block;

  padding: 7px 25px 8px 25px;

  font-family: 'Oswald', sans-serif;
  font-size: 16px;
  color: #fff;
  text-transform: uppercase;

  background: #f8686e;
  border: 0;
  border-radius: 25px;

  transition: 0.2s;
}
.button:hover {
  background: #f82e34;
}
.button:disabled {
  opacity: 0.7;
  background: #f8686e;
}
